import React, { Component } from "react";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";
import Axios from "axios";
import validasiMutasiCiok from "../../Validations/validasiMutasiCiok";
import { getTimbangan } from "../../GetTimbangan";
import { renderFieldSelect } from "../../renderField";

const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormMutasiCiok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listBarang: [],
    };
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  componentDidMount() {
    Axios.get(server + "subcategorys2?kodeKelompok=PERHIASAN")
      .then((res) =>
        this.setState({
          listBarang: res.data,
        })
      )
      .catch((err) => console.log(err));
    getTimbangan(this.props.change);
  }

  handleChange(data) {
    var supp = data ? data : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
      kadarCetak: result_explode[1],
      kadarBeli: result_explode[2],
      namaJenis: result_explode[3],
    });
    this.props.change("kadar", result_explode[1]);
    this.props.change("kadarTransaksi", result_explode[2]);
    this.props.change("keterangan", result_explode[3]);
    this.props.change("noFaktur", this.props.noFaktur);
  }

  setKadar() {
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarBeli);
    this.props.change("keterangan", this.state.namaJenis);
  }
  setNetto() {
    this.props.change(
      "netto",
      this.props.netto === "NaN" ? 0 : this.props.netto
    );
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-3 form-group">
            <Field
              label="Kode Barang"
              name="kodeBarang"
              component={renderFieldSelect}
              onChange={(data) => this.handleChange(data)}
              onBlur={() => this.setKadar()}
              options={this.state.listBarang.map((list) => {
                let data = {
                  value:
                    list.kodeJenis +
                    "|" +
                    list.kadarCetak +
                    "|" +
                    list.kadarBeli +
                    "|" +
                    list.namaJenis,
                  name: list.namaJenis,
                };
                return data;
              })}
            ></Field>
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="bruto"
              label="Bruto (Gr)"
              component={renderField}
              type="text"
              onBlur={this.setNetto()}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="kadarTransaksi"
              label="Harga (Gr)"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="netto"
              label="Netto (Gr)"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-md-3 form-group d-none">
            <Field
              name="keterangan"
              label="Netto (Gr)"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-md-3 form-group d-none">
            <Field
              name="noFaktur"
              label="Nomor Faktur"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-md-12 col-lg-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormMutasiCiok = reduxForm({
  form: "FormMutasiCiok",
  enableReinitialize: true,
  validate: validasiMutasiCiok,
})(FormMutasiCiok);
const selector = formValueSelector("FormMutasiCiok"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarTransaksi } = selector(state, "bruto", "kadarTransaksi");
  return {
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
  };
})(FormMutasiCiok);
